* {
	box-sizing: border-box;
	padding: 0;
	margin: 0;
}

html {
	font-size: 16px;
}

body {
	background: #eee;
	font-family: "Source Code Pro", Helvetica, monospace;
}

header {
	padding: 2rem 4rem;
}

header h1 {
	color: #0f4c75;
	width: 10vw;
}

.container {
	margin: 0 auto;
	width: 30vw;
	position: relative;
}

.exclamation_container {
	margin: 4rem 0;
	display: flex;
	height: 15rem;
}

.exclamation {
	z-index: 1;
	align-self: center;
	margin: 0 auto;
	height: 7rem;
	animation: scale 3s linear infinite;
}

.box {
	transform: translate(-50%, 0);
	left: 50%;
	position: absolute;
	background: rgba(0, 183, 194, 0.5);
	width: 15rem;
	height: 15rem;
	animation: rotate2 30s linear infinite;
}

.box + .box {
	transform: translate(-50%, 0) rotate(45deg);
	animation: rotate 60s linear infinite;
}

.userhistory {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.userhistory * {
	margin: 2rem 0 0 0;
	color: #0f4c75;
	text-align: center;
}

.userhistory h1 {
	text-transform: uppercase;
	margin-top: 3rem;
}

.userhistory p {
	color: #1b262c;
	width: 80%;
}

.button {
	font-size: 1.2rem;
	font-weight: 600;
	text-decoration: none;
	text-transform: uppercase;
	background: #0f4c75;
	color: #eee;
	padding: 1em 3em;
	margin-bottom: 4em;
	transition: background 0.2s linear;
}

.button:hover {
	background: #07689f;
	transition: background 0.2s linear;
}

footer {
	text-align: center;
	padding: 1em;
	color: #1b262c;
}

.mainContainer {
	height: 100vh;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

.App {
	flex: 1;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	color: #1b262c;
}

.App h1 {
	margin-bottom: 2rem;
}

@keyframes rotate {
	0% {
		transform: translate(-50%, 0) rotate(45deg);
	}
	100% {
		transform: translate(-50%, 0) rotate(405deg);
	}
}

@keyframes rotate2 {
	0% {
		transform: translate(-50%, 0) rotate(360deg);
	}
	100% {
		transform: translate(-50%, 0) rotate(0deg);
	}
}

@keyframes scale {
	50% {
		transform: scale(1.2);
	}
	100% {
		transform: scale(1);
	}
}

@media (max-width: 720px) {
	header {
		padding: 1rem 0;
		text-align: center;
	}
	header h1 {
		width: 100vw;
	}

	.container {
		margin: 0 auto;
		width: 90vw;
		position: relative;
	}

	.exclamation_container {
		margin: 1.5rem 0;
		height: 7rem;
	}

	.exclamation {
		height: 3.5rem;
	}

	.box {
		width: 7rem;
		height: 7rem;
	}

	.userhistory h1 {
		margin-top: 1.5rem;
	}

	.userhistory * {
		margin-top: 2rem;
	}

	.button {
		margin-bottom: 2em;
	}
}

@media only screen and (min-width: 720px) and (max-width: 1200px) {
	header {
		padding: 2rem 4rem;
		text-align: left;
	}
	header h1 {
		width: 100vw;
	}

	.container {
		margin: 0 auto;
		width: 50vw;
		position: relative;
	}

	.exclamation_container {
		margin: 1.5rem 0;
		height: 15rem;
	}

	.exclamation {
		height: 8rem;
	}

	.box {
		width: 15rem;
		height: 15rem;
	}

	.userhistory h1 {
		margin-top: 3.5rem;
	}

	.userhistory * {
		margin-top: 2rem;
	}

	.button {
		margin-bottom: 2em;
	}
}
